export default {
  data() {
    return {
      // 密码输入的内容
      passVal: "",
      // 控制按钮的颜色
      isActive: false
    };
  },
  methods: {
    // 当密码框有输入内容的时候
    inputVal() {
      this.isActive = true;
    },
    // 当密码框失去焦点的时候
    inputBlur() {
      if (this.passVal !== "") {
        return (this.isActive = true);
      }
      return (this.isActive = false);
    },
    // 点击登录按钮
    login() {
      if (this.passVal !== "") {
        return uni.navigateTo({
          url: "/pages/login/login"
        });
      }
      return uni.showToast({
        title: "请输入登录密码",
        duration: 2000,
        icon: "none"
      });
    }
  }
};
